.header {
  background-color: #fff;
  padding: 15px;
  .toggle-sidebar {
    cursor: pointer;
  }
  .header-icon {
    background-color: #f9f9f9;
    padding: 10px;
    border-radius: 50%;
    color: #555;
    cursor: pointer;

    margin-left: 10px;
  }

  .notifications-container {
    display: inline-block;
    margin-left: 20px;

    .dropdown-menu {
      min-width: 600px;
      padding: 20px;
      border: none;
      top: 35px !important;
      left: auto !important;
      transform: none !important;
      padding: 0;
      &::before {
        content: "";
        position: absolute;
        top: -20px;
        left: 16px;
        border-style: solid;
        border-width: 10px;
        border-color: transparent transparent #333 transparent;
      }
      .noti-head {
        background-color: #333;
        padding: 15px;
        color: #fff;
        font-weight: bold;
        span {
          float: right;
        }
      }
      .noti-content {
        padding: 20px;
        ul {
          margin: 0;
          li {
            padding: 5px;
            font-weight: bold;
            content: #777;
            border-bottom: 1px solid #e9e8e8;
            margin-bottom: 10px;
            font-size: 15px;
            &:first-child {
              padding-top: 0;
            }
            &:last-child {
              padding-bottom: 0;
              margin-bottom: 0;
              border-bottom: none;
            }
            i {
              background-color: #f7f7f7;
              width: 30px;
              height: 30px;
              line-height: 30px;
              text-align: center;
              margin-right: 10px;
              text-align: center;
              margin-right: 10px;
              position: relative;
              top: -5px;
              font-size: 14px;
              color: #fff;
              &.fa-envelope {
                background-color: #4caf50;
              }
              &.fa-money-bill-wave {
                background-color: #2196f3;
              }
              &.fa-thumbs-up {
                background-color: #ff9800;
              }
            }
          }
        }
      }
      .noti-foot {
        a {
          display: block;
          padding: 10px;
          border-top: 1px solid #eee;
          text-decoration: none;
          &:hover {
            text-align: center;
          }
        }
      }
    }
  }
  .userMenu-container {
    float: right;
    .username {
      cursor: pointer;
      .avatar {
        width: 40px;
        height: 40px;
      }
      .user {
        font-weight: bold;
        color: #777;
        margin-right: 10px;
        margin-left: 10px;
      }
    }
    .dropdown-menu {
      min-width: 600px;
      padding: 20px;
      border: none;
      top: 40px !important;
      left: auto !important;
      right: 0 !important;
      transform: none !important;
      border: 1px solid #ddd;
      &::before {
        content: "";
        position: absolute;
        top: -20px;
        right: 40px;
        border-style: solid;
        border-width: 10px;
        border-color: transparent transparent #ddd transparent;
      }
      .user-info {
        h3 {
          font-weight: bold;
        }
        p {
          color: #888;
        }
      }
      .activities-list {
        li {
          padding: 10px;
        }
      }
      .userMenu-links {
        .link-box {
          background-color: #fff;
          padding: 10px 15px;
          border-radius: 6px;
          text-align: right;
          border: 1px solid #f3f3f3;
          cursor: pointer;
          i {
            float: left;
            position: relative;
            top: 12px;
            &.fa-gear {
              color: #2980b9;
            }
            &.fa-user {
              color: #16a085;
            }
            &.fa-power-off {
              color: #c0392b;
            }
          }
        }
      }
    }
  }
}
