.page.home-page {
  .content-area {
    @media #{$maxSmall} {
      width: 100% !important;
    }

    .content {
      .page-body {
        .chart {
          border: 3px solid #c0392b;
          max-width: 1000px;
          margin: auto; 
          display: none;
          &.active{
            display: block;
          }
        }
        .info {
          display: flex;
          li {
            &.active {
              background-color: $flatColor1;
              color: $flatWhite;
            }
            background-color: $flatWhite;
            color: $flatColor1;
            border-radius: 5px;
            border: 1px solid $flatWhite;
            cursor: pointer;
            box-shadow: inset 0px 0px 3px 1px $flatColor1;
            padding: 8px;
            margin-left: 5px;
          }
        }

        .table-type {
          display: none;
          &.active {
            display: block;
          }
        }
        form {
          margin-bottom: 30px;
          display: flex;
          gap: 10px;
          align-items: center;
          flex-wrap: wrap;
          .check {
            appearance: none;
            -webkit-appearance: none;
            width: 30px;
            height: 30px;
            border: 5px solid $flatWhite;
            border-radius: 5px;
            position: relative;
            overflow: hidden;
            transition: $transition;

            &:checked:before {
              transform: scale(1) rotate(0deg);
              transition: $transition;
            }

            &::before {
              content: "\f00c";
              font-family: var(--fa-style-family-classic);
              font-weight: 900;
              position: absolute;
              top: 0;
              left: 0;
              font-size: 14px;
              background-color: $flatColor1;
              color: white;
              transform: scale(0) rotate(360deg);
              transition: 0.3s;
              width: 100%;
              height: 100%;
              display: flex;
              align-items: center;
              justify-content: center;
            }
          }
          .input-con {
            border-radius: 6px;

            @media #{$maxMobile} {
              width: 100%;
            }

            &:not(:last-child) {
              background-color: $flatWhite;
              color: #961414;

              padding: 5px;
            }

            flex-grow: 1;
            .title {
              display: block;
              margin-bottom: 10px;
              font-weight: 700;
              text-align: center;
            }
            input,
            select {
              width: 100%;
              text-align: center;
              background-color: #fff;
              height: 30px;
              border: none;
              outline: none;
              color: $flatColor1;
              box-shadow: inset 1px 1px 6px 0px $flatColor2;
            }
            input[type="number"] {
              /* إزالة الأسهم في وضع التحويم */
              &::-webkit-inner-spin-button,
              &::-webkit-outer-spin-button {
                -webkit-appearance: none;
                margin: 0;
              }
              -moz-appearance: textfield;
            }
            .price-t {
              text-align: center;
              display: block;
              background-color: #fff;
              color: #000;
              height: 30px;
              padding: 5px;
            }
            .add {
              i {
                margin-left: 5px;
              }
              cursor: pointer;
              background-color: $flatBlue;
              color: #fff;
              padding: 5px;
              border: none;
              min-width: 100px;
              width: 100%;
              border-radius: 8px;
              background: $flatColor3;
              box-shadow: inset -21px 21px 35px #961414;
            }
          }
        }
        .full-info {
          display: none;
        }
        .table-container {
          overflow-x: scroll;
          width: 100%;
          overflow-y: hidden;

          &::-webkit-scrollbar {
            width: 15px;
          }

          &::-webkit-scrollbar-track {
            background-color: $flatBlackAlt;
            cursor: pointer;
            display: none;
            @media #{$maxMobile} {
              display: block;
            }
          }

          &::-webkit-scrollbar-track:hover {
            background-color: $flatBlack;
          }

          // &::-webkit-scrollbar-thumb {
          //     background-color: $purple1;
          // }

          .info-table {
            margin-top: 30px;
            min-width: 665px;
            width: 100%;
            border-spacing: 1;
            box-shadow: inset -1px 4px 40px $flatWhite;
            border-radius: 5px;
            color: $flatWhite;
            thead {
              background-color: $flatWhite;
              color: $flatColor1;
            }
            td {
              padding: 7px;
              border: 1px solid $flatBlack;
              text-align: center;

              i {
                cursor: pointer;
                margin-left: 10px;
              }
              span {
                margin-right: 5px;
                &.category {
                  display: block;
                  rotate: 90deg;
                }
              }
              .show-more {
                color: $flatBlue;
                @include hover($flatBlueAlt, "color");
              }
              .edit {
                color: $flatGreen;
                @include hover($flatGreenAlt, "color");
              }
              .delete {
                color: $flatRed;
                @include hover($flatRedAlt, "color");
              }
            }
          }
        }
      }
    }
  }
}
