.login {
  margin: auto;
  width: 350px;
  margin-top: calc(100vh / 3);
  background-color: #f6f0eb;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  .h4 {
    color: $flatColor1;;
  }
  img {
    width: 100px;
  }
  input {
    padding: 5px;
    margin: auto;
    display: block;
    margin-bottom: 5px;
    box-shadow: inset 1px 1px 6px 0px $flatColor2;
    border: none;
    outline: none;
    width: 175px;
  }
  .submit {
    width: 175px;
    cursor: pointer;
    background-color: $flatColor3;
    color: #fff;
  }
}
