.page {
  @media #{$maxSmall} {
    flex-direction: column;
  }

  display: flex;
  .sidebar {
    @media #{$maxSmall} {
      width: 100% !important;
      height: auto !important;
      display: flex;
      justify-content: space-between;
    }

    width: 250px;
    top: 0;
    left: 0;
    height: 100%;
    background-color: $flatWhite;
    padding: 15px;
    height: 100vh;
    &.no-sidebar {
      left: -250px;
    }
    .logo-area {
      font-weight: bold;
      font-size: 32px;
      margin-bottom: 30px;
      img {
        width: 100px;
        margin-right: 50px;
      }
    }

    .toggle-menu {
      display: none;
      @media #{$maxMobile} {
        display: block;
      }
      cursor: pointer;
      font-size: 30px;
    }
    .links-area {
      //all Links
      @media #{$maxSmall} {
        display: flex;
        flex-wrap: wrap;
      }
      @media #{$maxMobile} {
        display: none;
        position: absolute;
        background-color: white;
        width: 100%;
        top: 110px;
        left: 0;
        z-index: 1000;
        border-top: 1px solid $flatColor2;
        &.open {
          display: flex;
        }
      }

      li {
        @media #{$maxMobile} {
          width: 100%;
          padding: 10px;
        }

        a {
          color: $flatColor1;
          padding: 10px 20px;
          display: block;
          font-weight: bold;
          transition: 0.3s;
          &:hover {
            text-decoration: none;
            color: $flatColor3;
          }
        }
      }
      //main Links
      > li > a {
        position: relative;
        &::before {
          content: "";
          position: absolute;
          top: 50%;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          border: 2px solid #fff;
          transform: translateY(-50%);

          right: 0;

          @media #{$maxSmall} {
            display: none;
          }
        }

        @media #{$maxSmall} {
          @include hover($flatOrange, "color");
        }

        @media #{$minMedium} {
          &:hover {
            padding-right: 25px;
          }
        }
        @media #{$maxMobile} {
          &:hover {
            padding-right: 25px;
          }
        }

        &.active {
          &::before {
            border-color: $flatOrange;
          }
        }
      }
    }
  }
}
