* {
  box-sizing: border-box;
}
body {
  font-family: "Cairo", sans-serif;
  direction: rtl;

  ul {
    list-style: none;
  }
  a {
    text-decoration: none;
  }
}

.page {
  min-height: 100vh;
  .content-area {
    position: relative;
    width: calc(100% - 250px);
    min-height: 100vh;
    transition: 0.3s;
    float: right;
    will-change: width;
    background-image: url(../images/background.png);
    background-size: cover;
    .overlay {
      position: absolute;
      background-color: #96141463;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
    &.no-sidebar {
      width: 100%;
    }
  }
  .page-head {
    padding: 20px;
    font-weight: bold;
  }

  .container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    z-index: 1;
    position: relative;
  }
}
